<template>
	<div>
		<div class="search">
			<span class="close_search" onclick="javascript:history.go(-1)"></span>
			<input type="text" class="search-input" v-model="keyword" placeholder="输入游戏名称或拼音">
		</div>
		<div class="search-content" v-show="keyword">
			<ul>
				<li v-for="item of list" @click="chooseFun(item.game,item.id)">{{item.game}}</li>
				<div v-show="hasNoData" class="noDataTips">没有找到匹配的游戏</div>
			</ul>
		</div>
	</div>
</template>
<script type="text/javascript">
	import axios from "axios";
	import {mapMutations} from "vuex";
	export default{
		props:{game:Object},
		data (){
			return {
				keyword:'',
				list:[],
				timer:null
			}
		},
		computed:{

			hasNoData (){
				return !this.list.length
			}
		},
		methods:{
			...mapMutations({myGameChange:'myGameChange'}),
			chooseFun (game,id){
				this.$store.commit("myGameChange",{"name":game,"id":id})
				// this.$store.dispatch("gameChoose",{"game":game,"id":id})
				// this.bus.$emit("changegame",id)
				var type=this.$route.query.will;
				if(type=="Rent"){		
					this.myGameChange({"name":game,"id":id});
					this.$router.push("/"+type)
				}else if(type=="Publish"){
					this.bus.$emit("mypublish",game,id)
					this.$router.replace("/"+type)
				}
				
			}
		},
		watch:{
			keyword (){
				if(this.timer){
					clearTimeout(this.timer)
				}
				if(!this.keyword){
					this.list=[]
					return;
				}
				this.timer=setTimeout(()=>{
					const result=[]
					for(var i in this.game){
						this.game[i].forEach((value)=>{
							if(value.game.indexOf(this.keyword)>-1){
								result.push(value)
							}
						})
					}
					this.list=result;
				}, 100);
			}
		}
	}
</script>
<style type="text/css" scoped>
.close_search{
	width: 30px;
	height: 30px;
	background: url(../../assets/img/return.png) center center no-repeat;
	background-size: 28px;
	position: absolute;
	left: 8px;
	top: 8px;
}
.search{
	height: 50px;
	background: #e6292c;
	padding: 6px 5px 6px 40px;
	box-sizing: border-box;
	position: relative;
}

.search-input{
	width: 100%;
	text-align: center;
	border-radius: 3px;
	color: #666;
	-webkit-appearance:none;
	box-sizing: border-box;
	height: 38px;
	line-height: 38px;
	border: 0px;
}
.search-content{
	position: absolute;
	top: 50px;
	left: 0;
	right: 0;
	bottom: 0;
	background: #fff;
	z-index: 1;
}
.search-content li{
	line-height: 11vw;
	height: 11vw;
	border-bottom: 1px solid #eee;
	padding-left: 0.1rem;
}

</style>


